









import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';

@Component
export default class CNow extends Vue {
  // Data
  private dataNow: string = (new Date()).toLocaleString();

  // Lifecycle hook
  created(): void {
    this.getNow();
  }

  // Methods
  private getNow(): void {
    setInterval((): void => {
      let now: string = (new Date()).toLocaleString();
      if (this.dataNow !== now) {
        this.dataNow = now;
      }
    }, 200);
  }
}
